import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ConcertsProvider} from '../../providers/concerts/concerts';

/**
 * Generated class for the MottakPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-mottak',
  templateUrl: 'mottak.html',
})
export class MottakPage {
  concert_id: any;
  usersWithTickets: any;
  concert: any;
  private _concertService: ConcertsProvider;

  constructor(public navCtrl: NavController, public navParams: NavParams, private concertService: ConcertsProvider) {
    this.concert = navParams.get('concert');
    this.refreshData(concertService);
  }

  // noinspection JSUnusedGlobalSymbols
  static ionViewDidLoad() {
    console.log('ionViewDidLoad MottakPage');
  }

  goToUserTickets(user) {
    this.navCtrl.push('EditUserPage', {'user': user});
  }


  private refreshData(concertService: ConcertsProvider) {
    this._concertService = concertService;
    this.concertService.getUsersWithTicketToConcert(this.concert._id).then(users => {
      this.usersWithTickets = users;
      console.log(this.usersWithTickets);
    });
  }

}



